.loading {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 9999;
  background: #000; }
  .loading .boxLoading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
    .loading .boxLoading .circle {
      box-sizing: border-box;
      width: 80px;
      height: 80px;
      border-radius: 100%;
      border: 5px solid rgba(255, 255, 255, 0.2);
      border-top-color: #FFF;
      animation: spin 1s infinite linear; }

@keyframes spin {
  100% {
    transform: rotate(360deg); } }

header {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  z-index: 2; }
  @media screen and (max-width: 767px) {
