  header ul.navbar-nav li.nav-item {
    position: relative;
    display: block;
    margin: 0rem 0.625rem; }
    header ul.navbar-nav li.nav-item:after {
      content: '';
      width: 100%;
      height: 0%;
      background-image: transparent;
      opacity: 1;
      position: absolute;
      left: 0;
      top: -0.9375rem;
      z-index: 1;
      transition: 500ms; }
      @media screen and (max-width: 767px) {
        header ul.navbar-nav li.nav-item:after {
          position: initial;
          content: initial; } }
    @media screen and (max-width: 767px) {
      header ul.navbar-nav li.nav-item.active a.nav-link {
        color: #be1e2d; } }
    header ul.navbar-nav li.nav-item.active:after {
      height: 160%;
      opacity: 1;
      background-image: linear-gradient(to bottom, #be1e2d, rgba(187, 29, 44, 0)); }
      @media screen and (max-width: 767px) {
        header ul.navbar-nav li.nav-item.active:after {
          background-image: initial; } }
    header ul.navbar-nav li.nav-item:hover:after {
      height: 160%;
      opacity: 1;
      background-image: linear-gradient(to bottom, #be1e2d, rgba(187, 29, 44, 0)); }
      @media screen and (max-width: 767px) {
        header ul.navbar-nav li.nav-item:hover:after {
